import React from 'react'
import useWindowScroll from 'react-use/lib/useWindowScroll'
import styled from 'styled-components/macro'

import { mediaDesktop, mediaMobile, mediaSmallMobile } from '../app/Responsive'
import { PageLayoutGrid } from '../atoms/PageLayoutGrid'
import { PageTitle } from '../atoms/PageTitle'
import { SoundControl } from '../atoms/SoundControl'
import { LogoutWidget } from '../organisms/LogoutWidget'
import { SecureRoute } from '../routes/SecureRoute'
import { Spacing } from '../styling'
import { HomeButton } from './HomeButton'

interface ICommonProps {
  topContent?: React.ReactNode
}

interface IStyledProps extends ICommonProps {
  isScrolledDown: boolean
}

export interface IPageLayoutProps extends ICommonProps {
  pageTitle?: string
  hideLogoutWidget?: boolean
  showSoundControl?: boolean
  showHomeButton?: boolean
  useBackButton?: boolean
  backRoute?: string
  isForIconDashboard?: boolean
  className?: string
}

const StyledPageLayoutResponsive = styled.div<IStyledProps>`
  .page-header {
    position: sticky;
    top: 0;
    width: 100vw;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    z-index: 30;
    height: ${Spacing.PageHeaderHeight + Spacing.DefaultSizeUnit};
    background-color: ${({ isScrolledDown }) =>
      isScrolledDown ? 'rgba(0, 0, 0, 0.7)' : 'initial'};
  }

  .home-button-container {
    flex: 0 0 auto;
    z-index: 100;
  }

  .menu-container {
    flex: 1 1 0;
    align-self: flex-start;
    z-index: 80;
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    overflow: hidden;
    ${mediaDesktop`
    height: 4rem;
    `};
    ${mediaMobile`
    height: 3.7rem;
    `};
  }

  .menu-item-title {
    flex: 1 1 0;
    ${mediaMobile`
      display: ${({ topContent }: IPageLayoutProps) =>
        topContent !== undefined ? 'none' : 'initial'};
    `};
  }

  .menu-item {
    ${mediaDesktop`
      margin: ${Spacing.Middle} ${Spacing.Small};
    `};
    ${mediaMobile`
     margin: 0 ${Spacing.Small};
    `};
  }

  .menu-item-logout {
    ${mediaSmallMobile`
      display: ${({ topContent }: IPageLayoutProps) =>
        topContent !== undefined ? 'none' : 'initial'};
    `};
  }
`

export const PageLayoutResponsive: React.FC<IPageLayoutProps> = ({
  pageTitle = '',
  hideLogoutWidget = false,
  showSoundControl = false,
  showHomeButton = true,
  useBackButton = false,
  backRoute,
  children,
  topContent,
  isForIconDashboard = false,
  className,
}) => {
  const { y } = useWindowScroll()

  return (
    <StyledPageLayoutResponsive
      topContent={topContent}
      isScrolledDown={y > 10}
      className={className}
    >
      {pageTitle && <PageTitle title={pageTitle} />}

      <div className="page-header">
        <div className="home-button-container">
          {showHomeButton ? (
            <HomeButton goBack={useBackButton} backRoute={backRoute} />
          ) : null}
        </div>

        <div className="menu-container">
          <div className="menu-item-title">
            <PageTitle.Display />
          </div>

          <div className="menu-item">{topContent}</div>

          {showSoundControl && (
            <div className="menu-item">
              <SoundControl />
            </div>
          )}

          <div className="menu-item-logout">
            {!hideLogoutWidget && (
              <SecureRoute path="/" component={LogoutWidget} redirect={false} />
            )}
          </div>
        </div>
      </div>
      <PageLayoutGrid isForIconDashboard={isForIconDashboard}>
        {children}
      </PageLayoutGrid>
    </StyledPageLayoutResponsive>
  )
}
