import styled from 'styled-components/macro'

import { Spacing } from '../styling'

interface IProps {
  isForIconDashboard: boolean
}

const getGridTemplatesRows = (props: IProps) =>
  props.isForIconDashboard
    ? `repeat(4, ${Spacing.DashboardButtonHeight + Spacing.DefaultSizeUnit})`
    : 'repeat(16, auto)'

const getGridGap = (props: IProps) =>
  props.isForIconDashboard ? `${Spacing.Middle}` : `0 ${Spacing.Small}`

export const PageLayoutGrid = styled.div<IProps>`
  display: grid;
  width: 100vw;
  max-width: 100%;
  grid-gap: ${getGridGap};
  grid-template-columns: repeat(16, 1fr);
  grid-template-rows: ${getGridTemplatesRows};
  padding: 0 ${Spacing.Small} ${Spacing.Small} ${Spacing.Small};
  grid-auto-flow: row dense;
`
