import { useAutorun, useInterval, useReaction } from '@speedlo/hooks'
import { useAsObservableSource } from 'mobx-react-lite'
import useAudio from 'react-use/lib/useAudio'

import { useRoot } from '../atoms/RootProvider'

interface IOptions {
  soundUrl: string
  shouldPlay: boolean
  intervalSec: number
}

export function useIntervalSound({
  soundUrl,
  shouldPlay,
  intervalSec,
}: IOptions) {
  const [audio, state, controls] = useAudio({
    src: soundUrl,
    autoPlay: false,
    loop: false,
  })

  const { user, config } = useRoot()
  const source = useAsObservableSource({
    shouldPlay,
    isPlaying: state.isPlaying,
  })

  const canPlay = () => {
    return config.soundEnabled && user.soundEnabled
  }

  const playIfYouCan = () => {
    if (canPlay() && source.shouldPlay) {
      controls.volume(0.5)
      controls.play()
    }
  }

  useInterval(playIfYouCan, intervalSec * 1000)

  useReaction(() => source.shouldPlay, playIfYouCan)

  useAutorun(() => {
    if (!canPlay() || !source.shouldPlay) {
      controls.pause()
      controls.seek(0)
    }
  })

  return audio
}
