import { VolumeOff, VolumeUp } from '@material-ui/icons'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { TouchButton } from '../molecules/TouchButton'
import { Colors } from '../styling'
import { useRoot } from './RootProvider'
import { SizerButton } from './SizerButton'

export const SoundControl: React.FC = () => {
  const { user } = useRoot()
  return useObserver(() => (
    <SizerButton>
      <TouchButton
        color={user.soundEnabled ? Colors.green : Colors.red}
        icon={user.soundEnabled ? VolumeUp : VolumeOff}
        iconSize={2}
        onClick={() => {
          user.controlSound(!user.soundEnabled)
        }}
      />
    </SizerButton>
  ))
}
