import { SvgIconComponent } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components/macro'

import { logError } from '../helpers/logger'
import { Colors, Spacing } from '../styling'

interface IProps {
  icon?: SvgIconComponent
  iconName?: string
  color?: string
  fontSize?: number
  fontSizeUnit?: string
}

const StyledIcon = styled.span<IProps>`
  color: ${({ color }) => color};
  > svg {
    font-size: ${({ fontSize, fontSizeUnit }) => `${fontSize}${fontSizeUnit}`};
  }
`

export const Icon = React.memo<IProps>(
  ({
    iconName,
    icon,
    children,
    color = Colors.white,
    fontSize = 1.5,
    fontSizeUnit = Spacing.DefaultSizeUnit,
  }) => {
    if (iconName) {
      // tslint:disable-next-line: no-console
      logError(
        `Icon: Do not use iconName! Replace "${iconName}" with the one imported from @material-ui/icons and pass in icon prop instead.`,
      )
      return null
    }
    if (icon && children) {
      // tslint:disable-next-line: no-console
      logError(
        `Icon: Do not use both icon prop & children! Only one of them is allowed.`,
      )
      return null
    }
    const iconNode = icon ? React.createElement(icon) : children
    return (
      <StyledIcon
        color={color}
        fontSize={fontSize}
        fontSizeUnit={fontSizeUnit}
        className="materialIcon"
      >
        {iconNode}
      </StyledIcon>
    )
  },
)
