import { RM } from '@speedlo/tools'
import { Instance, types } from 'mobx-state-tree'

import { BaseModel } from '../../../models/BaseModel'

const DEFAULT_IMPORT = false
const DEFAULT_QUANTITY = null
const DEFAULT_MAX = Number.MAX_SAFE_INTEGER
const DEFAULT_ITEM = {
  import: DEFAULT_IMPORT,
  quantity: DEFAULT_QUANTITY,
  max: DEFAULT_MAX,
}
export const MIN_TRANSFER_QUANTITY = 0

type TInitItem = {
  id: string
  max: number
}

const Item = types.model('Item', {
  id: types.identifier,
  import: types.optional(types.boolean, DEFAULT_IMPORT),
  quantity: types.optional(types.maybeNull(types.number), null),
  max: types.optional(types.number, DEFAULT_MAX),
})

export const StockTransferModel = BaseModel.named('StockTransfer')
  .props({
    transferItems: types.map(Item),
  })
  .volatile(() => ({
    importMode: false,
    targetBranchId: null as MaybeID,
  }))
  .views(self => ({
    get isImportMode() {
      return self.importMode === true
    },
    get isTransferMode() {
      return self.importMode === false
    },
    get targetId() {
      return self.targetBranchId !== self.root.settings.branchId
        ? self.targetBranchId
        : null
    },
    get sourceId() {
      return self.root.settings.branchId
    },
    get isImportingAll() {
      const shouldNotImportSome = Array.from(self.transferItems.values()).find(
        RM.propEq('import', false),
      )
      return self.transferItems.size > 0 && !shouldNotImportSome
    },
    isImportingItem(itemId: ID) {
      const item = self.transferItems.get(itemId) || DEFAULT_ITEM
      return item.import
    },
    getItemTransferQuantity(itemId: ID) {
      const item = self.transferItems.get(itemId) || DEFAULT_ITEM
      return item.quantity
    },
    getItemLimit(itemId: ID) {
      const item = self.transferItems.get(itemId)
      return item ? item.max : 0
    },
    hasInvalidAmount(itemId: ID) {
      const item = self.transferItems.get(itemId) || DEFAULT_ITEM
      if (item.quantity === null) {
        return false
      }
      const amountUnderflow = item.quantity < MIN_TRANSFER_QUANTITY
      const amountOverflow = item.quantity > item.max
      return amountUnderflow || amountOverflow
    },
    get itemsForTransfer() {
      return Array.from(self.transferItems.values()).filter(item =>
        this.isImportMode
          ? item.import === true
          : item.quantity !== null && item.quantity > 0,
      )
    },
  }))
  .actions(self => ({
    setTargetId(branchId: MaybeID) {
      self.targetBranchId = branchId
    },
    initItems(items: RoA<TInitItem>) {
      const newItems = items.map(({ id, max }) => {
        const item = self.transferItems.get(id) || DEFAULT_ITEM
        return { ...item, id, max }
      })
      this.clearItems()
      newItems.forEach(item => {
        self.transferItems.put(item)
      })
    },
    toggleImportAll() {
      const newValue = !self.isImportingAll
      self.transferItems.forEach(item => (item.import = newValue))
    },
    toggleImportItem(itemId: ID) {
      const item = self.transferItems.get(itemId) || DEFAULT_ITEM
      self.transferItems.put({ id: itemId, ...item, import: !item.import })
    },
    setQuantity(itemId: ID, quantity: number) {
      const item = self.transferItems.get(itemId) || DEFAULT_ITEM
      self.transferItems.put({
        id: itemId,
        ...item,
        quantity: Number.isNaN(quantity) ? null : quantity,
      })
    },
    clearTransferState() {
      self.transferItems.forEach(item => {
        item.quantity = DEFAULT_QUANTITY
        item.import = DEFAULT_IMPORT
      })
    },
    clearItems() {
      self.transferItems.clear()
    },
    toggleImportMode() {
      self.importMode = !self.importMode

      if (self.importMode) {
        self.transferItems.forEach(item => (item.quantity = DEFAULT_QUANTITY))
      }
    },
  }))

export interface TStockTransferModel
  extends Instance<typeof StockTransferModel> {}
