import styled from 'styled-components/macro'

import { mediaDesktop, mediaMobile } from '../app/Responsive'

export interface IPageLayoutGridItemProps {
  gridRow?: string | number
  gridRowInMobile?: string | number
  gridRowSpan?: number
  gridRowSpanInMobile?: number
  gridColumn?: string | number
  gridColumnInMobile?: string | number
  gridColumnSpan?: number
  gridColumnSpanInMobile?: number
}

const gridColumnProperty = ({
  gridColumn = '1',
  gridColumnSpan = 1,
}: IPageLayoutGridItemProps) =>
  gridColumn.toString().indexOf('/') === -1
    ? `${gridColumn} / span ${gridColumnSpan}`
    : gridColumn

const gridRowProperty = ({
  gridRow = '1',
  gridRowSpan = 1,
}: IPageLayoutGridItemProps) =>
  gridRow.toString().indexOf('/') === -1
    ? `${gridRow} / span ${gridRowSpan}`
    : gridRow

const gridColumnInMobileProperty = ({
  gridColumn = '1',
  gridColumnInMobile = 1,
  gridColumnSpanInMobile = 16,
}: IPageLayoutGridItemProps) => {
  if (gridColumnInMobile.toString().indexOf('/') === -1) {
    return `${gridColumnInMobile} / span ${gridColumnSpanInMobile}`
  } else {
    return gridColumnInMobile
  }
}

const gridRowInMobileProperty = ({
  gridRow = '1',
  gridRowInMobile,
  gridRowSpanInMobile = 1,
}: IPageLayoutGridItemProps) => {
  if (gridRowInMobile === undefined) {
    return 'unset'
  }
  if (gridRowInMobile.toString().indexOf('/') === -1) {
    return `${gridRowInMobile} / span ${gridRowSpanInMobile}`
  } else {
    return gridRowInMobile
  }
}

export const PageLayoutGridItem = styled.div<IPageLayoutGridItemProps>`
  ${mediaDesktop`
    grid-column: ${gridColumnProperty};
    grid-row: ${gridRowProperty};
  `};
  ${mediaMobile`
    grid-column: ${gridColumnInMobileProperty};
    grid-row: ${gridRowInMobileProperty};
  `};
`
