import { SvgIconComponent } from '@material-ui/icons'
import React, { MouseEvent } from 'react'
import styled from 'styled-components/macro'

import { mediaDesktop, mediaMobile } from '../app/Responsive'
import { Icon } from '../atoms/Icon'
import { logError } from '../helpers/logger'
import { applyAlphaToColor, Colors, Radiuses, Spacing } from '../styling'

export interface ITouchButtonProps
  extends React.HTMLAttributes<HTMLButtonElement> {
  icon?: SvgIconComponent
  iconName?: string
  iconColor?: Colors
  iconSize?: number
  text?: string
  color?: Colors
  badge?: ReactNode
  disabled?: boolean
  textNextToIcon?: boolean
  className?: string
  width?: string
  height?: string
  onClick?(e: MouseEvent<HTMLButtonElement>): void
}

const applyAlpha = (alphaValue: number) => (props: ITouchButtonProps) =>
  applyAlphaToColor(props.color!, alphaValue)

const StyledTouchButton = styled.button`
  min-height: ${Spacing.ButtonHeight + Spacing.DefaultSizeUnit};
  min-width: ${Spacing.ButtonHeight + Spacing.DefaultSizeUnit};
  width: ${({ width = '100%' }: ITouchButtonProps) => width};
  height: ${({ height = '100%' }: ITouchButtonProps) => height};
  margin: 0;
  padding: 0 0.2rem;
  border: none;
  border-radius: ${Radiuses.Button};
  background-color: ${applyAlpha(0.7)};
  color: white;
  position: relative;
  user-select: none;
  /* fallback font size for incompatible browsers */
  font-size: 1rem;
  ${mediaDesktop`
    font-size: calc(0.6rem + 0.5vw);
  `}
  ${mediaMobile`
    font-size: calc(0.9rem + 0.5vw);
  `};
  transition-duration: 0.2s;

  & .materialIcon {
    vertical-align: middle;
  }
  &:hover:enabled {
    background-color: ${applyAlpha(0.9)};
  }
  &:active:enabled {
    background-color: ${applyAlpha(1)};
  }
  &:disabled {
    opacity: 0.3;
    cursor: not-allowed;
  }
`

const ButtonLabelStyled = styled.div`
  color: white;
  text-align: center;
  width: 100%;
  overflow: hidden;
`
const ButtonLabelNextToIconStyled = styled(ButtonLabelStyled)`
  width: calc(100% - 4rem);
  margin: 0 0 0 1rem;
  text-align: left;
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
`

export const TouchButton = React.forwardRef<
  HTMLButtonElement,
  ITouchButtonProps
>(
  (
    {
      children,
      icon,
      iconName,
      iconColor = Colors.white,
      iconSize = 3,
      color = Colors.greyMiddle,
      textNextToIcon = false,
      badge,
      text,
      ...props
    },
    forwardedRef,
  ) => {
    if (iconName) {
      logError(
        `TouchButton: Do not use iconName prop! Replace "${iconName}" with the one imported from @material-ui/icons and pass in icon prop instead.`,
      )
    }
    return (
      <StyledTouchButton color={color} {...props} ref={forwardedRef}>
        {icon && <Icon icon={icon} color={iconColor} fontSize={iconSize} />}

        {text && textNextToIcon && (
          <ButtonLabelNextToIconStyled>{text}</ButtonLabelNextToIconStyled>
        )}

        {text && !textNextToIcon && (
          <ButtonLabelStyled>{text}</ButtonLabelStyled>
        )}

        {badge}
        {children}
      </StyledTouchButton>
    )
  },
)
